: var(--primary-light);\n          margin-right: var(--spacer-1);\n        }\n\n        > span {\n          display: inline;\n        }\n      }\n    }\n  }\n\n  &__stage {\n    flex: 1;\n    overflow: hidden;\n    user-select: none;\n  }\n\n  &__thumb,\n  &__thumb img {\n    display: none;\n\n    @media (min-width: 992px) {\n      display: block;\n      width: 100%;\n      height: auto;\n    }\n  }\n\n  &__open {\n    display: none;\n\n    @media (min-width: 992px) {\n      font-weight: var(--font-bold);\n      font-size: var(--font-size-lg);\n      padding: 0 var(--spacer-2);\n      display: inline-block;\n      margin-bottom: var(--spacer-2);\n    }\n  }\n\n  &__big-image {\n    cursor: zoom-in;\n  }\n\n  img {\n    user-select: none;\n    -webkit-user-drag: none;\n  }\n}\n\n$pswp__assets-path: \"https://cdn.jsdelivr.net/npm/photoswipe@4.1.3/dist/default-skin/\";\n$pswp__root-z-index: 1999;\n\n@import \"photoswipe/src/css/main\";\n@import \"photoswipe/src/css/default-skin/default-skin\";\n\n.pswp {\n  &__caption__center {\n    @media (min-width: 992px) {\n      text-align: center;\n    }\n  }\n\n  .btn {\n    position: fixed;\n    bottom: 20px;\n    right: 20px;\n  }\n}\n","/*! PhotoSwipe main CSS by Dmitry Semenov | photoswipe.com | MIT license */\n\n/*\n\tStyles for basic PhotoSwipe functionality (sliding area, open/close transitions)\n*/\n\n// PhotoSwipe uses Autoprefixer, so vendor prefixed are added automatically when needed.\n\n@import \"main-settings\";\n\n \n/* pswp = photoswipe */\n.pswp {\n\tdisplay: none;\n\tposition:absolute;\n\twidth: 100%;\n\theight: 100%;\n\tleft:0;\n\ttop:0;\n\toverflow: hidden;\n\t-ms-touch-action: none;\n\ttouch-action: none;\n\tz-index: $pswp__root-z-index;\n\t-webkit-text-size-adjust: 100%;\n\t/* create separate layer, to avoid paint on window.onscroll in webkit/blink */\n\t-webkit-backface-visibility: hidden;\n\toutline: none;\n\n\t@if $pswp__box-sizing-border-box == true {\n\t\t* {\n\t\t\tbox-sizing: border-box;\n\t\t}\n\t}\n\t\n\timg {\n\t\tmax-width: none;\n\t}\n}\n\n/* style is added when JS option showHideOpacity is set to true */\n.pswp--animate_opacity {\n\t/* 0.001, because opacity:0 doesn't trigger Paint action, which causes lag at start of transition */\n\topacity: 0.001;\n\twill-change:opacity;\n\t/* for open/close transition */\n\ttransition: opacity $pswp__show-hide-transition-duration cubic-bezier(.4,0,.22,1);\n}\n\n.pswp--open {\n\tdisplay: block;\n}\n\n.pswp--zoom-allowed .pswp__img {\n\t/* autoprefixer: off */\n\tcursor: -webkit-zoom-in;\n\tcursor: -moz-zoom-in;\n\tcursor: zoom-in;\n}\n\n.pswp--zoomed-in .pswp__img {\n\t/* autoprefixer: off */\n\tcursor: -webkit-grab;\n\tcursor: -moz-grab;\n\tcursor: grab;\n}\n\n.pswp--dragging  .pswp__img {\n\t/* autoprefixer: off */\n\tcursor: -webkit-grabbing;\n\tcursor: -moz-grabbing;\n\tcursor: grabbing;\n}\n\n/*\n\tBackground is added as a separate element.\n\tAs animating opacity is much faster than animating rgba() background-color.\n*/\n.pswp__bg {\n\tposition: absolute;\n\tleft: 0;\n\ttop: 0;\n\twidth: 100%;\n\theight: 100%;\n\tbackground: $pswp__background-color;\n\topacity: 0;\n\ttransform: translateZ(0);\n\t-webkit-backface-visibility: hidden;\n\twill-change:opacity;\n}\n\n.pswp__scroll-wrap {\n\tposition: absolute;\n\tleft: 0;\n\ttop: 0;\n\twidth: 100%;\n\theight: 100%;\n\toverflow:hidden;\n}\n\n.pswp__container,\n.pswp__zoom-wrap {\n\t-ms-touch-action: none; \n\ttouch-action: none;\n\tposition: absolute;\n\tleft: 0;\n\tright: 0;\n\ttop: 0;\n\tbottom: 0;\n}\n\n/* Prevent selection and tap highlights */\n.pswp__container,\n.pswp__img {\n\t-webkit-user-select: none;\n\t-moz-user-select: none;\n\tuser-select: none;\n\t-webkit-tap-highlight-color: rgba(0,0,0,0);\n\t-webkit-touch-callout: none;\n}\n\n.pswp__zoom-wrap {\n\tposition: absolute;\n\twidth: 100%;\n\t-webkit-transform-origin: left top;\n\t-moz-transform-origin: left top;\n\t-ms-transform-origin: left top;\n\ttransform-origin: left top;\n\t/* for open/close transition */\n\ttransition: transform $pswp__show-hide-transition-duration cubic-bezier(.4,0,.22,1);\n}\n\n.pswp__bg {\n\twill-change: opacity;\n\t/* for open/close transition */\n\ttransition: opacity $pswp__show-hide-transition-duration cubic-bezier(.4,0,.22,1);\n}\n\n.pswp--animated-in {\n\t.pswp__bg,\n\t.pswp__zoom-wrap {\n\t\t-webkit-transition: none;\n\t\ttransition: none;\n\t}\n}\n\n.pswp__container,\n.pswp__zoom-wrap {\n\t-webkit-backface-visibility: hidden;\n}\n\n.pswp__item {\n\tposition: absolute;\n\tleft: 0;\n\tright: 0;\n\ttop: 0;\n\tbottom: 0;\n\toverflow: hidden;\n}\n\n.pswp__img {\n\tposition: absolute;\n\twidth: auto;\n\theight: auto;\n\ttop: 0;\n\tleft: 0;\n}\n\n/*\n\tstretched thumbnail or div placeholder element (see below)\n\tstyle is added to avoid flickering in webkit/blink when layers overlap\n*/\n.pswp__img--placeholder {\n\t-webkit-backface-visibility: hidden;\n}\n\n/*\n\tdiv element that matches size of large image\n\tlarge image loads on top of it\n*/\n.pswp__img--placeholder--blank {\n\tbackground: $pswp__placeholder-color;\n}\n\n.pswp--ie .pswp__img {\n\twidth: 100% !important;\n\theight: auto !important;\n\tleft: 0;\n\ttop: 0;\n}\n\n/*\n\tError message appears when image is not loaded\n\t(JS option errorMsg controls markup)\n*/\n.pswp__error-msg {\n\tposition: absolute;\n\tleft: 0;\n\ttop: 50%;\n\twidth: 100%;\n\ttext-align: center;\n\tfont-size: 14px;\n\tline-height: 16px;\n\tmargin-top: -8px;\n\tcolor: $pswp__error-text-color;\n}\n\n.pswp__error-msg a {\n\tcolor: $pswp__error-text-color;\n\ttext-decoration: underline;\n}\n","$pswp__show-hide-transition-duration: 333ms !default;\n$pswp__controls-transition-duration: 333ms !default;\n$pswp__background-color: #000 !default;\n$pswp__placeholder-color: #222 !default;\n$pswp__box-sizing-border-box: true !default; // disable .pswp * { box-sizing:border-box } (in case you already have it in your site css)\n$pswp__root-z-index: 1500 !default;\n$pswp__assets-path: '' !default; // path to skin assets fol